@media (min-width: 360px) {
  .header {
    width: 100%;
    height: 568px;
    display: block;
    position: relative;
    background-image: url(../images/header/bg-image.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-origin: border-box;

    .upperline {
      justify-content: space-between;
      display: flex;
      position: absolute;
      width: 100%;
      height: 72px;
      align-items: center;

      .upperline-title {
        margin-left: 20px;
      }

      .upperline-actions {
        display: flex;
        gap: 24px;
        width: auto;
        height: 32px;
        margin-right: 20px;
        transition: 0.5s;
      }

      .link-menu {
        width: 32px;
        height: 32px;
        background-image: url(../images/header/icon-menu.svg);
        transition: 0.4s;
      }

      .link-menu,
      .link-number {
        width: 24px;
        height: 24px;
        background-size: cover;
        cursor: pointer;
      }

      .menu-toggle {
        display: none;
      }

      .link-menu:checked {
        scale: 9;
      }

      .link-number {
        width: 24px;
        height: 24px;
        background-image: url(../images/header/Icon-Phone-call.svg);
        transition: 0.4s;
      }

      .upperline-actions-number {
        top: 18px;
        display: flex;
        opacity: 1;
        user-select: auto;
        padding-right: 10px;
        position: absolute;
        right: 96px;
        color: #1B2129;
        text-align: right;
        font-family: Inter, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 700;
        line-height: 16px;
        letter-spacing: 2px;
        text-transform: uppercase;
        transition: 0.5s;
        margin-top: 8px;
        transition-delay: 0.5s;
      }

      .number-text {
        align-self: center;
        display: block;
      }
    }

    .bottomline {
      position: absolute;
      bottom: 40px;
      left: 20px;

      .bottomline-text {
        color: #FFF;
        font-family: Manrope, sans-serif;
        font-size: 36px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%;
      }
    }


    .link-menu-close {
      margin-right: 20px;
      width: 32px;
      height: 32px;
      background-image: url(../images/header/Icon-close.svg);
    }

    .menu {
      width: 100%;
      top: 0;
      right: 0;
      left: 0;
      position: fixed;

      height: 760px;
      display: flex;
      background-color: #F9F9F9;
      opacity: 0;
      pointer-events: none;
      transition: 0.5s;
    }

    .menu:target {
      opacity: 1;
      pointer-events: all;
    }

    .menu__upperline {
      justify-content: space-between;
      display: flex;
      position: absolute;
      width: 100%;
      height: 72px;
      align-items: center;
    }

    .menu-upperline-title {
      margin-left: 20px;
      width: 152px;
      height: 16px;
    }

    .menu_content {
      margin-top: 152px;
      margin-left: 120px;
      position: fixed;
      display: block;
    }

    .menu_content-link1 {
      margin-bottom: 32px;
      color: #1B2129;
      font-family: Manrope, sans-serif;
      font-size: 22px;
      font-style: normal;
      font-weight: 700;
      line-height: 22px; /* 100% */
      transition: 0.5s;
    }

    .menu_content-link2 {
      margin-bottom: 32px;
      color: #1B2129;
      font-family: Manrope, sans-serif;
      font-size: 22px;
      font-style: normal;
      font-weight: 700;
      line-height: 22px; /* 100% */
      transition: 0.5s;
    }

    .menu_content-link3 {
      margin-bottom: 32px;
      color: #1B2129;
      font-family: Manrope, sans-serif;
      font-size: 22px;
      font-style: normal;
      font-weight: 700;
      line-height: 22px; /* 100% */
      transition: 0.5s;
    }

    .menu_content-link4 {
      margin-bottom: 32px;
      color: #1B2129;
      font-family: Manrope, sans-serif;
      font-size: 22px;
      font-style: normal;
      font-weight: 700;
      line-height: 22px; /* 100% */
      transition: 0.5s;
    }

    .line-in-menu1 {
      top: 180px;
      left: -300px;
      position: fixed;
      height: 2px;
      width: 155px;
      background-color: #1B2129;;
      opacity: 0;
      transition: 0.5s;
    }

    .line-in-menu2 {
      top: 225px;
      left: -300px;
      position: fixed;
      height: 2px;
      width: 113px;
      background-color: #1B2129;;
      opacity: 0;
      transition: 0.5s;
    }

    .line-in-menu3 {
      top: 270px;
      left: -300px;
      position: fixed;
      height: 3px;
      width: 94px;
      background-color: #1B2129;;
      opacity: 0;
      transition: 0.5s;
    }

    .line-in-menu4 {
      top: 316px;
      left: -300px;
      position: fixed;
      height: 2px;
      width: 115px;
      background-color: #1B2129;;
      opacity: 0;
      transition: 0.5s;
    }

    .menu_content-link1:hover ~ .line-in-menu1 {
      left: 122;
      opacity: 1;
    }

    .menu_content-link2:hover ~ .line-in-menu2 {
      left: 122;
      opacity: 1;
    }

    .menu_content-link3:hover ~ .line-in-menu3 {
      left: 122;
      opacity: 1;
    }

    .menu_content-link4:hover ~ .line-in-menu4 {
      left: 122;
      opacity: 1;
    }

    .in-menu-number {
      top: 800px;
      color: #1B2129;
      font-family: Manrope, sans-serif;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: 140%; /* 22.4px */
      letter-spacing: 2px;
      text-transform: uppercase;
    }

    .call-to-order-menu {
      color: #1B2129;
      font-family: Manrope, sans-serif;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: 140%; /* 22.4px */
      letter-spacing: 2px;
      text-transform: uppercase;
    }

    .line-call-to-order {
      position: absolute;
      width: 144px;
      height: 1.5px;
      background-color: #1B2129;
    }
  }
}

